.darkBG {
  background: hsla(203, 12%, 21%, 1);
  /* background: linear-gradient(to top, #7f9790, #8ea8a0 60%, #e8e1d4); */
  width: 100%;
  height: 100%;
  z-index: 0;
  top: 0;
  left: 0;
  position: absolute;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 100%;
}

