.modal-name {
  grid-row: 1/2;
  grid-column: 3/5;
  padding-right: 0.7em;
  padding-bottom: 0.7em;
  align-self: start;
  justify-self: start;
  font-size: 0.7em;
  font-weight: 900;
  color: #faf8eb;

  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.modal button {
  background: #8ea8a0;
  display: inline-block;
  border-radius: 15px;
  width: fit-content;
}

.hmm {
  grid-row: 3/4;
  visibility: hidden;
}

.durations-list {
  height: 60%;
